.App {
    text-align: center;
    padding: 290px 0 200px;
    line-height: 28px;
    font-size: 16px;
    height: 100%;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    color: #6b707f;
    letter-spacing: 0.15px;
    background: #ccefff;
}

body{
  background: #fff;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  background: #f7f7f7;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.nav_btn:not(.not-round-btn) {
  color: #4c4cf1;
  border-radius: 45px;
}
.right-nav .nav_btn {
  font-size: 14px;
  font-weight: 500;
  padding: 8px 27px;
  text-align: center;
  border-radius: 4px;
  border: 2px solid #fff;
  transition: all 0.3s linear;
  background: #fff;
  color: #4c4cf1;
}

.cloud-bg .bg1 {
  position: absolute;
  width: 100%;
  height: 400px;
  background: url("https://srt2prxml.chdo.xyz/footer-cloud.png");
  background-repeat: repeat-x;
  background-position: 0 bottom;
  bottom: 39%;
}

.theme-share-head{
  width: 44px;
    height: 44px;
    overflow: hidden;
    padding: 3px;
}
.acss-header {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -240px 0 0 -230px;
  z-index: 1;
}
.acss_banner {
  width: 333px;
  height: 69px;
  background-image: url(https://srt2prxml.chdo.xyz/logo.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  margin: 0 auto 70px;
}
.verify-form {
  margin: 0 auto;
  text-align: center;
  font-size: 14px;
  position: relative;
  background: #fff;
  background-color: rgba(255,255,255,0.5);
  border-radius: 4px;
  box-shadow: 0 0 10px rgb(171 198 235 / 30%);
}
.avatar {
  display: inline-block;
  vertical-align: middle;
  padding: 15px 0px 15px 0px;
}

.theme-share-head span {
  line-height: 1;
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}
.theme-share-head, .theme-share-head>span {
  width: 370px;
  height: 44px;
  overflow: hidden;
}

.theme-share-head img {
  width: 100%;
  vertical-align: middle;
}

.verify-input {
  padding: 20px 30px 48px;
}

.verify-input .pickpw {
  padding: 0 0 15px;
}

